<template >
  <v-col v-if="dados.length > 0">
    <v-row >
      <v-col id="tituli_sessao">
        <h1>{{titulo}}</h1>
        <v-spacer></v-spacer>
        <v-btn
          class="ma-3"
          outlined
          color="teal"
          @click="orderFunction"
        >
          <v-icon>mdi-sort</v-icon>
        </v-btn>
      </v-col>
    </v-row>                 
    <v-row>
      <v-card @click="dialigAtualiza(item)" v-for="item in dados" :key="item.id" id="cell_card">
      <v-card-text>
        <!-- <h3>{{ item.display_tipo }}</h3> -->
        <h3>Posto {{ item.display_posto }}</h3>
        <h3>Valor: R${{ item.valor }}</h3>
      </v-card-text>
    </v-card>
    </v-row>
     <v-dialog
      v-model="dialog"
      scrollable
      max-width="300px"
    >
      <v-card>
        <v-card-title>Atualizar valor</v-card-title>
        <v-divider></v-divider>
        <v-card-text style="height: auto;">
          <v-radio-group
            v-model="dialogm1"
            column
          >
            <h3>Posto {{ valueAtt.display_posto }}</h3>
            <h3>Valor: R${{ valueAtt.valor }}</h3>
            <v-text-field
              label="Digite o novo valor"
              type="number"
              v-model="novoValor"
            />
          </v-radio-group>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-btn
            color="blue darken-1"
            text
            @click="dialog = false"
          >
            Fechar
          </v-btn>
          <v-btn
            color="blue darken-1"
            text
            @click="atualizador"
          >
            Salvar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-col>
</template>

<script>
  import {Atualizador} from '../service/Axios'

export default {

  props: {
    dados: {
      type: [Array],
      default: null
    },
    titulo: {
      type: [String],
      default: null
    }
  },
  data () {
    return {
      dialogm1: '',
      dialog: false,
      valueAtt:{},
      novoValor: 0.00
    }
  },
  methods: {
    orderFunction() {
      this.dados  = this.dados.sort(function (a, b) {
        return (a.valor > b.valor) ? 1 : ((b.valor > a.valor) ? -1 : 0);
      });
    },
    dialigAtualiza(value) {
      this.valueAtt = value
      this.novoValor = value.valor
      this.dialog = !this.dialog
    },
    async atualizador() {
      let numero = parseFloat(this.novoValor)
      try {
        await Atualizador(this.valueAtt.id, numero)
        this.dados.map(item =>{
          if(item.id === this.valueAtt.id) {
            item.valor = numero
          }
        })
        this.$emit('atualiza')
        this.dialog = false
      } catch (error) {
        this.dialog = false
      }
    }
  }
}
</script>

