#tituli_sessao {
  display: flex;
  flex-direction: row;
}
#cell_card {
  background-color: #ADD8E6;
  color: black;
  display: flex;
  align-content: center;
  justify-content: center;
  width: 30%;
  margin: 1%;
  max-height: 6.5rem;
  margin-left: 2%;
}
h3{
  color: #000000;
}
</style>