<template>
  <div>
    <v-app-bar color="black" dark>
      <!-- <v-app-bar-nav-icon @click.stop="drawer = !drawer"></v-app-bar-nav-icon> -->
      <v-toolbar-title id="toolbar_title">WVFuels</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn depressed color="teal" outlined @click="logout">
        Logout
      </v-btn>
    </v-app-bar>


    <v-navigation-drawer
      v-model="drawer"
      absolute
      temporary
      clipped
      disable-resize-watcher
    >
      <v-list
        nav
        dense
      >
        <v-list-item-group
          v-model="group"
          active-class="deep-purple--text text--accent-4"
        >
        <h3>Postos de combustvel</h3>
        <v-divider></v-divider>
          <v-list-item v-for="item in postos" :key="item.id">
            <v-list-item-content>
              <v-list-item-title > {{ item.nome }} </v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>

    <v-app>
      <v-main>
        <v-container fluid>
          <v-row>
            <v-col>
              <!-- Primeira linha gasolinas -->
              <v-row>
                <Lista v-on:atualiza="dataAtualiza" titulo="Seo Gasolina Comum" :dados="Gasolina"/>
                <Lista v-on:atualiza="dataAtualiza" titulo="Seo Gasolina Adtivada" :dados="GasolinaAd"/>
              </v-row>
              <!-- Segunda linha Diesel -->
              <v-row>
                <Lista v-on:atualiza="dataAtualiza" titulo="Seo Diesel Comum" :dados="Diesel"/>
                <Lista v-on:atualiza="dataAtualiza" titulo="Seo Diesel S-10" :dados="DieselS"/>
              </v-row>
              <!-- Terceira linha Etanois -->
              <v-row>
                <Lista v-on:atualiza="dataAtualiza" titulo="Seo Etanol Comum" :dados="Etanol"/>
                <Lista v-on:atualiza="dataAtualiza" titulo="Seo Etanol Hidratado" :dados="EtanolHi"/>
              </v-row>
            </v-col>
          </v-row>
        </v-container>
      </v-main>
    </v-app>
  </div>
</template>

<script>
import {Postos, Combustiveis} from '../service/Axios'
import Lista from "../components/Lista.vue"

export default {
  name: 'App',
  components: {
    Lista
  },
  data: () => ({
    postos: [],
    combustiveis: [],
    Gasolina: [],
    GasolinaAd: [],
    Etanol: [],
    EtanolHi: [],
    Diesel: [],
    DieselS:[],
    drawer: false,
    group: null,
    update: false
  }),
  methods: {
    logout() {
      localStorage.clear();
      this.$router.push('/')
    },
    filterCombus(){
      this.combustiveis.forEach(item => {
        if(item.tipo === 0) {
          this.postos.forEach(posto => {
            if(posto.id === item.posto) {
              item.display_posto = posto.nome
            }
          })
          item.display_tipo = 'Gasolina comum';
          this.Gasolina.push(item)
        } else if (item.tipo === 1) {
          this.postos.forEach(posto => {
            if(posto.id === item.posto) {
              item.display_posto = posto.nome
            }
          })
          item.display_tipo = 'Gasolina aditivada';
          this.GasolinaAd.push(item)
        } else if (item.tipo === 3) {
          this.postos.forEach(posto => {
            if(posto.id === item.posto) {
              item.display_posto = posto.nome
            }
          })
          item.display_tipo = 'Etanol anidro';
          this.Etanol.push(item)
        } else if (item.tipo === 4) {
          this.postos.forEach(posto => {
            if(posto.id === item.posto) {
              item.display_posto = posto.nome
            }
          })
          item.display_tipo = 'Etanol hidratado';
          this.EtanolHi.push(item)
        } else if (item.tipo === 6) {
          this.postos.forEach(posto => {
            if(posto.id === item.posto) {
              item.display_posto = posto.nome
            }
          })
          item.display_tipo = 'Diesel comum';
          this.Diesel.push(item)
        }else {
          this.postos.forEach(posto => {
            if(posto.id === item.posto) {
              item.display_posto = posto.nome
            }
          })
          item.display_tipo = 'Diesel S-10';
          this.DieselS.push(item)
        }
      });
    },
    async dataAtualiza() {
      // console.log("chamou emit")
      // window.location.reload()
      this.$forceUpdate();
    },
    async loadPostos() {
      const {data: postos} = await Postos()
      this.postos = postos
    },
    async loadCombustiveis() {
      const {data: combustiveis} = await Combustiveis()
      this.combustiveis = combustiveis
      this.filterCombus()
    }
  },
  async created(){
    await this.loadPostos()
    await this.loadCombustiveis()
  }
};
</script>

<style>
#toolbar_title {
  font-size: 33px;
  margin: auto;
  justify-content: center;
}
</style>